@import "../../styles/General.scss";

.step3__container {
  @include formSize($height: 500px);
  padding-left: 110px;

  .user-input {
    margin-top: 66px;
  }

  textarea {
    display: block;
    width: 680px;
    height: 84px;
    margin-top: 8px;
    padding: 0.65rem 0.5rem;
  }

  &_buttons {
    display: flex;
    margin-top: 48px;
    justify-content: space-between;

    :first-child button {
      @include button(
        $width: 73px,
        $border: 1.5px solid $button-bg-color,
        $color: $button-bg-color,
        $background-color: $button-color
      );
    }

    :nth-child(2) {
      @include button($width: 103px);
      margin-right: 88px;
    }
  }
}
