@import "../../styles/General.scss";

.step2__container {
  @include formSize;
  padding-left: 110px;

  .user-input {
    margin-top: 66px;

    div {
      position: relative;
      margin-top: 8px;
    }

    img {
      position: absolute;
      width: 15px;
      height: 15px;
      top: 20%;
      left: 56%;
    }

    button {
      @include button(
        $width: 44px,
        $border: 1.5px solid $button-bg-color,
        $color: $button-bg-color,
        $background-color: $button-color
      );
      margin-top: 8px;
    }
  }

  &_checkboxes input {
    width: 16px;
    height: 16px;
    border: 1.5px solid rgba(0, 0, 0, 0.24);
    border-radius: 4px;
  }

  &_radios input {
    width: 20px;
    height: 20px;
    border: 1.5px solid rgba(0, 0, 0, 0.24);
    border-radius: 100px;
  }

  &_buttons {
    display: flex;
    margin-top: 48px;
    justify-content: space-between;

    :first-child button {
      @include button(
        $width: 73px,
        $border: 1.5px solid $button-bg-color,
        $color: $button-bg-color,
        $background-color: $button-color
      );
    }

    :nth-child(2) {
      @include button($width: 73px);
      margin-right: 98px;
    }
  }
}
